*,
*::before,
*::after {
  box-sizing: border-box;
}

.App {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0px 0px 30px rgb(189, 189, 189);
  padding: 2rem;
  border-radius: 2px;
}

.mouse__coordinates {
  min-width: 50%;
  padding: 1rem;
  margin: auto;
  margin-bottom: 1rem;
  font-size: 2rem;
  font-weight: bold;
}

.mouse__coordinates--orange {
  background-color: coral;
  color: white;
}

.mouse__coordinates--red {
  background-color: crimson;
  color: white;
}

.mouse__coordinates--green {
  background-color: olivedrab;
  color: white;
}

.mouse__coordinates--blue {
  background-color: slateblue;
  color: white;
}

.optionsbar__container {
  width: auto;
  min-height: 100px;
  margin-top: 3rem;
  padding: 1rem;
  box-shadow: 0px 0px 20px gray;
  font-weight: bold;
}

select {
  /*Remove browser styles*/
  appearance: none;
  background-color: transparent;
  border: none;
  padding: 0 1em 0 0;
  margin: 0;
  width: 100%;
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  line-height: inherit;
  outline: none;
}

.optionsbar {
  background-color: dimgray;
  min-width: 15ch;
  max-width: 30ch;
  border: 1px solid grey;
  border-radius: 0.25em;
  padding: 0.5em 0.5em;
  font-size: 1.25rem;
  margin: 1rem auto 0rem;
  cursor: pointer;
  line-height: 1.3;
  background-image: linear-gradient(to top, #f9f9f9, #fff 33%);
}
